@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}

body{
  font-family: 'Raleway';
}
.App
{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  
  padding:60px 30px 0px 30px ;
  flex:1.5;
  flex:var(--sidebar-flex);
  background: rgb(35, 35, 36);
  background: linear-gradient(180deg, rgb(10, 10, 10) 50%, rgba(84,90,97,1) 80%);
}


nav.imageWrapper{
    position: relative
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}
.one {
  grid-column: 1;
  grid-row: 1;
}
.two {
  grid-column: 2;
  grid-row: 1;
}
.three {
  grid-column: 3;
  grid-row: 1;
}
.condiv
{
  padding: 100px ;
  background-color: white;
  flex:6;
  flex:var(--condiv-flex);
}

.contact
{
 display: flex;
 justify-content: center;
 flex-direction: column;
 align-items: center;
 
  
}

.section{
  border:4px solid;
}

.content
{
  display: flex;
  flex-direction:row;
  width: 100%;
  height: auto;
  align-items: center;
  padding:100px;
}

.social
{
  position: absolute;
  display: flex;
  bottom: 100px;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color:orange;
}
.social i:hover
{
  color:black;
}

.img {
  max-width: 86%;
  height: auto;
}


