.box1, .box2, .box3, .box4, .box5{
  background-color: #ffffff;
  border-radius: 10px 10px 10px 10px;
  overflow :hidden;
  height: 200px;
  max-height: 35vh;
  -webkit-box-shadow: 0px 12px 18px -6px rgba(0,0,0,0.3);
  box-shadow: 0px 12px 18px -6px rgba(0,0,0,0.3);
  width: 250px;
}
.box{
  margin: 20px;
}
.boxshadow{
  box-shadow: 0px 12px 18px -6px rgba(0,0,0,0.3);
}
.grid{
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.grid > div {
  flex-basis: calc(20% -40px);
}